.loading-spinner-container {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 200px; 
  height: 200px; 
  opacity: 0; /* Göm containern som standard */
  transition: opacity 0.3s ease; /* Gör övergången smidig */
} 

.loading-spinner {
  width: 200px;
  height: 200px;
  display: flex;
  justify-content: center; 
  align-items: center; 
  background: transparent; /* Ändra bakgrundsfärgen till transparent för att ta bort den vita rutan */
  visibility: visible; /* Visa spinnern när den laddas */
}

.spinner {
  width: 100%;
  height: 100%;
  position: relative;
  transform: translateZ(0) scale(1);
  backface-visibility: hidden;
  transform-origin: 0 0;
}
  
.spinner div { 
  box-sizing: content-box;
}
  
@keyframes spinner-rotate {
  0% { transform: rotate(0deg) }
  100% { transform: rotate(360deg) }
}
  
.spinner > div > div {
  transform-origin: 100px 100px;
  animation: spinner-rotate 3.0303030303030303s linear infinite;
  opacity: 0.8; 
}
  
.spinner > div > div > div {
  position: absolute;
  left: 30px;
  top: 30px;
  width: 70px;
  height: 70px;
  border-radius: 70px 0 0 0;
  transform-origin: 100px 100px
}
  
.spinner > div div:nth-child(1) {
  animation-duration: 0.7575757575757576s;
}
  
.spinner > div div:nth-child(1) > div {
  background: #51cacc;
  transform: rotate(0deg);
}
  
.spinner > div div:nth-child(2) {
  animation-duration: 1.0101010101010102s;
}
  
.spinner > div div:nth-child(2) > div {
  background: #9df871;
  transform: rotate(0deg);
}
  
.spinner > div div:nth-child(3) {
  animation-duration: 1.5151515151515151s;
}
  
.spinner > div div:nth-child(3) > div {
  background: #e0ff77;
  transform: rotate(0deg);
}
  
.spinner > div div:nth-child(4) {
  animation-duration: 3.0303030303030303s;
}
  
.spinner > div div:nth-child(4) > div {
  background: #de9dd6;
  transform: rotate(0deg);
}