.modal-container {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    border: 1px solid grey;
    border-radius: 7%;
    background-color: rgba(255, 101, 90, 0.968);
    width: 30%;
    text-align: center;
}

.modal-text {
    display: grid;
    gap: 10px;
    list-style: none;
    justify-content: center;
    text-align: center;
    padding: 5px;
}

.closeBtn {
    margin: 0 auto;
    margin-bottom: 5px;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
}