.contact {
    margin: 25px auto;
    max-width: 800px;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
}
  
.contact-info {
    margin-bottom: 25px;
    padding: 25px;
    background-color: #fcfafa;
    border-radius: 5px;
    box-shadow: 0 5px 8px rgba(0, 0, 0, 0.1);
}
  
.contact-details {
    margin-bottom: 10px;
}
  
.contact-icons {
    flex: 1;
    padding: 10px;
    display: flex;
    justify-content: center;
}
  
.contact-icons .icon {
    margin-right: 10px; 
}

.image-container {
    width: 300px; 
    height: auto; 
}

#profile-image {
    max-width: 20%; 
    height: auto; 
    border-radius: 2%; 
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.5); 
    margin-bottom: 10px; 
    display: block; 
    margin-left: auto; 
    margin-right: auto; 
}