.about-container {
  margin: 25px auto; 
  max-width: 800px; 
}

#about-me {
  margin-bottom: 25px;
  padding: 20px;
  background-color: #fcfafa;
  border-radius: 5px;
  box-shadow: 0 5px 8px rgba(0, 0, 0, 0.1);
}
   
strong {
  font-weight: bold;
  color: #000;
}
  
em {
  font-style: italic;
  color: #000000;
}