.navbar {
    width: 100%;
    background: linear-gradient(to left, rgb(230, 217, 217), rgb(173, 144, 144), rgb(124, 96, 96));
    display: flex;
    justify-content: center; 
    align-items: center; 
    padding: 10px;
}

.menu-container {
    display: flex;
    align-items: center;
}

.nav-links {
    list-style: none;
    display: flex;
    margin: 0;
    padding: 0;
}

.nav-link {
    text-decoration: none;
    color: white;
    margin-right: 20px;
    padding: 10px;
    transition: color 0.3s, background-color 0.3s;
}

.nav-link:hover,
.nav-link:focus {
    color: #000000;
    background-color: #d4c0c0;
}

.nav-links li {
    margin: 10px 0;
}


/* Media Query för att justera navigationsmenyn för mindre skärmar */
@media only screen and (max-width: 768px) {
    .menu-container {
        flex-direction: column;
        align-items: flex-start; /* Justera för att placera menyn längst till vänster */
    }

    .nav-links {
        flex-direction: column; /* Visa länkarna i en kolumn på mindre skärmar */
        align-items: center; /* Centrera länkarna på mindre skärmar */
        width: 100%; /* Bredden på länkarna ska vara 100% */
        background-color: transparent; /* Genomskinlig bakgrundsfärg för menyn */
        padding: 20px; /* Justera padding för länkarna */
        border-radius: 10px; /* Avrunda hörnen på menyn */
        transition: opacity 0.3s ease; /* Lägg till en övergångseffekt för smidigare visning */
    }

    .nav-links.active {
        display: flex; /* Visa länkarna när menyn är aktiv */
    }

    .nav-links li {
        margin: 10px 0; /* Justera marginalerna mellan länkarna */
    }

    .nav-link {
        margin: 10px 0; /* Justera marginalerna för länkarna */
        font-size: 1.2em; /* Minska storleken på länkarna */
    }

    .menu-icon {
        display: block; /* Visa hamburgikonen på mindre skärmar */
        margin-right: 10px;
        font-size: 1.5em; /* Större storlek för hamburgikonen */
        color: white;
        cursor: pointer;
    }
}

.menu-icon i {
    font-size: 1.5em;
    color: white;
}

/* Stil för menyöverlägg för mindre skärmar */
.overlay {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.7);
    z-index: 999;
    display: none; /* Göm överlagret som standard */
}

.overlay.active {
    display: block; /* Visa överlagret när menyn är öppen */
}

.menu-items {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
}

.menu-items a {
    display: block;
    text-decoration: none;
    color: white;
    margin-bottom: 20px;
    font-size: 1.2em;
}

body { 
    font-family: 'Barlow', sans-serif;
    background-image: url(blommor.jpg);
    background-size: cover; /* Bakgrundsbilden täcker hela bakgrunden */
    background-repeat: no-repeat; /* Förhindra att bakgrundsbilden upprepas */
    background-position: center; /* Centrera bakgrundsbilden */
    /* Du kan också använda en specifik storlek, t.ex. 100px 100px */
}

h1, h2, h4 {
    font-family: 'Oswald', sans-serif;
}

h1 {
    font-size: 2.5em;
    color: #000000;
    text-align: center;
    margin-bottom: 20px;
}

h2 {
    font-size: 1.5em;
    color: #000000;
}

h4 {
    font-size: 1.5em;
    color: #000000;
}
    
p {
    font-size: 1.1em;
    line-height: 1.6;
    color: #000000;
}